@import "normalize.css";

body {
  --dark: #1b2125;
  --red: #b8424d;
  --white: #fcfbfc;
  --grey: #929dac;
  --dark-grey: #272a31;

  margin: 0;
  font-family: "Montserrat", sans-serif;
  color: var(--white);
  background: radial-gradient(at bottom right, var(--red) 0%, var(--dark) 70%);
  padding: 0px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.basic-button {
  border-radius: 15px;
  font-size: 20px;
  padding: 20px;
  border: none;
  box-shadow: none;
  color: var(--white);
  background-color: var(--dark-grey);
}

.basic-button:hover {
  transition: 0.5s;
  background-color: var(--grey);
}
